import React, { useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import {
  Menu,
  Icon,
  Input,
  Label,
  Dropdown,
  Button,
  Segment,
  Image,
  Header,
} from 'semantic-ui-react'
import { useMobileOrientation } from 'react-device-detect'
import {
  toggleUnit,
  searchCountriesByName,
  sortCountries,
  filterCountriesBySubRegion,
  filterCountriesByRegion,
} from '../reducers/countriesReducer'

import regions from '../regions'

import '../assets/css/App.css'
import github from '../assets/images/github.svg'

const HeaderNav = () => {
  const [value, setValue] = useState('')
  const [region, setRegion] = useState({
    id: 'FZUe47mEY9PCOzYmMxzYY',
    region: 'All',
    subregions: [],
  })

  const state = useSelector((state) => state)
  console.log('state', state)
  const recordsFound = useSelector((state) => state.countries.filtered)

  const activeRegion = useSelector(
    (state) => state.countries.filter.region.region
  ) //region object

  const { isLandscape } = useMobileOrientation()

  const activeSubregion = useSelector(
    (state) => state.countries.filter.subregion
  ) //subregion string

  const unit = useSelector((state) => state.unit.unit)

  const dispatch = useDispatch()

  const handleInput = (e) => {
    e.preventDefault()
    const input = e.target.value
    dispatch(searchCountriesByName(input))
    setValue(input)
  }

  const clearInput = () => {
    dispatch(searchCountriesByName(''))
    setValue('')
  }

  const changeUnit = (val) => {
    dispatch(toggleUnit(val))
  }

  const handleSort = (key) => {
    dispatch(sortCountries(key))
  }

  const handleRegionClick = (reg) => {
    dispatch(filterCountriesByRegion(reg))
    setRegion(reg)
    setValue('')
  }

  const handleSubregionClick = (sub) => {
    dispatch(filterCountriesBySubRegion(sub))
    setValue('')
  }

  const handleSubClose = () => {
    dispatch(filterCountriesByRegion(region))
  }

  return (
    <>
      <Menu attached="top" inverted borderless>
        <Menu.Item id="app-title" header>
          <p> World Countries</p>
        </Menu.Item>
        <Menu.Item id="mobile-header-button">
          <Button
            compact
            color="yellow"
            inverted
            content={unit === 'metric' ? 'imperial' : 'metric'}
            onClick={() =>
              changeUnit(unit === 'metric' ? 'imperial' : 'metric')
            }
          ></Button>
        </Menu.Item>
        <Menu.Item id="github">
          <Image
            as="a"
            href="https://github.com/I-keep-trying/countries3.1"
            alt="github"
            srcSet={`${github} 100w`}
            size="mini"
            src={github}
          />
        </Menu.Item>
      </Menu>

      {/* text input */}
      <Menu id="mobile-header" inverted vertical fluid>
        <Menu.Item id="mobile-header-item">
          <Input
            inverted
            size="mini"
            icon={
              <Icon
                link
                name={value.length > 0 ? 'close' : 'search'}
                onClick={value.length > 0 ? clearInput : handleInput}
              />
            }
            type="search"
            value={value}
            placeholder="Search by name"
            onChange={handleInput}
          />
        </Menu.Item>
      </Menu>
      {recordsFound.length > 1 ? (
        <Menu id="mobile-header2" inverted vertical fluid>
          <Menu.Item id="mobile-header2-item">
            {recordsFound.length}
            {' records found'}
          </Menu.Item>
        </Menu>
      ) : (
        <></>
      )}

      {/* sort menu */}

      {recordsFound.length === 1 ? (
        <>
          <Segment inverted textAlign="center">
            <Button
              labelPosition="left"
              icon="left chevron"
              basic
              color="yellow"
              content="All Countries"
              onClick={() => dispatch(searchCountriesByName(''))}
            />
            <Button
              labelPosition="left"
              icon="left chevron"
              basic
              color="yellow"
              content={recordsFound[0].region}
              onClick={() =>
                dispatch(filterCountriesByRegion(recordsFound[0].region))
              }
            />
            <Button
              labelPosition="left"
              icon="left chevron"
              basic
              color="yellow"
              content={activeSubregion}
              onClick={() =>
                dispatch(filterCountriesBySubRegion(activeSubregion))
              }
            />
          </Segment>{' '}
          <Segment inverted>
            <Header id="country-header" textAlign="center">
              {recordsFound[0].name.common}
            </Header>
          </Segment>
        </>
      ) : (
        <>
          <Menu inverted borderless size="tiny" id="mobile-filters">
            <Dropdown item inline text="Sort" defaultValue="Name">
              <Dropdown.Menu id="dropdown menu">
                <Dropdown.Item onClick={() => handleSort('name')}>
                  {state.countries.sortConfig.key === 'name' ? (
                    <Icon
                      name={
                        state.countries.sortConfig.direction === 'ascending'
                          ? 'arrow up'
                          : 'arrow down'
                      }
                    />
                  ) : (
                    <></>
                  )}
                  Name
                </Dropdown.Item>
                <Dropdown.Item onClick={() => handleSort('capital')}>
                  {state.countries.sortConfig.key === 'capital' ? (
                    <Icon
                      name={
                        state.countries.sortConfig.direction === 'ascending'
                          ? 'arrow up'
                          : 'arrow down'
                      }
                    />
                  ) : (
                    <></>
                  )}
                  Capital
                </Dropdown.Item>
                <Dropdown.Item onClick={() => handleSort('region')}>
                  {state.countries.sortConfig.key === 'region' ? (
                    <Icon
                      name={
                        state.countries.sortConfig.direction === 'ascending'
                          ? 'arrow up'
                          : 'arrow down'
                      }
                    />
                  ) : (
                    <></>
                  )}
                  Region
                </Dropdown.Item>
                <Dropdown.Item onClick={() => handleSort('subregion')}>
                  {state.countries.sortConfig.key === 'subregion' ? (
                    <Icon
                      name={
                        state.countries.sortConfig.direction === 'ascending'
                          ? 'arrow up'
                          : 'arrow down'
                      }
                    />
                  ) : (
                    <></>
                  )}
                  Subregion
                </Dropdown.Item>
                <Dropdown.Item onClick={() => handleSort('population')}>
                  {state.countries.sortConfig.key === 'population' ? (
                    <Icon
                      name={
                        state.countries.sortConfig.direction === 'ascending'
                          ? 'arrow up'
                          : 'arrow down'
                      }
                    />
                  ) : (
                    <></>
                  )}
                  Population
                </Dropdown.Item>
                <Dropdown.Item onClick={() => handleSort('area')}>
                  {state.countries.sortConfig.key === 'area' ? (
                    <Icon
                      name={
                        state.countries.sortConfig.direction === 'ascending'
                          ? 'arrow up'
                          : 'arrow down'
                      }
                    />
                  ) : (
                    <></>
                  )}
                  Area
                </Dropdown.Item>
              </Dropdown.Menu>
            </Dropdown>

            <Menu.Item position="right">
              Sorted by: {state.countries.sortConfig.key}
              {', '}
              {state.countries.sortConfig.direction}
            </Menu.Item>
          </Menu>

          {/* Filter By Region Dropdown */}
          {isLandscape ? (
            <Menu size="tiny" tabular widths={7}>
              {regions.map((r) => (
                <Menu.Item
                  key={r.id}
                  active={
                    state.countries.filter.region.region.toLowerCase() ===
                    r.region.toLowerCase()
                  }
                  onClick={() => handleRegionClick(r)}
                >
                  {r.region === 'All' ? 'All Regions' : r.region}
                </Menu.Item>
              ))}
            </Menu>
          ) : (
            <Menu inverted borderless size="tiny" id="mobile-filters">
              <Dropdown upward item inline text="Filter">
                <Dropdown.Menu>
                  {regions.map((r) => (
                    <Dropdown.Item
                      key={r.id}
                      text={r.region}
                      value={r.region}
                      onClick={() => handleRegionClick(r)}
                    />
                  ))}
                </Dropdown.Menu>
              </Dropdown>
              <Menu.Item position="right">
                {region.region !== 'All' ? (
                  <Label>
                    {region.region}
                    <Icon onClick={() => clearInput()} name="delete" />
                  </Label>
                ) : (
                  ''
                )}
              </Menu.Item>
            </Menu>
          )}

          {/* Filter By Subregion Dropdown */}

          {isLandscape && region.region !== 'All' ? (
            <Menu
              widths={state.countries.filter.region.subregions.length}
              tabular
              size="tiny"
            >
              {state.countries.filter.region.subregions.map((s) => (
                <Menu.Item
                  key={s.id}
                  active={
                    state.countries.filter.subregion.toLowerCase() ===
                    s.name.toLowerCase()
                  }
                  onClick={() => handleSubregionClick(s.name)}
                >
                  {s.name}
                </Menu.Item>
              ))}
            </Menu>
          ) : (
            <></>
          )}
          {!isLandscape && region.region !== 'All' ? (
            <Menu inverted borderless size="tiny" id="mobile-filters">
              <Dropdown item inline text="Subregion">
                <Dropdown.Menu>
                  {region.subregions.map((s) => (
                    <Dropdown.Item
                      key={s.id}
                      text={s.name}
                      value={s.name}
                      onClick={() => handleSubregionClick(s.name)}
                    />
                  ))}
                </Dropdown.Menu>
              </Dropdown>
              <Menu.Item position="right">
                {activeSubregion !== '' ? (
                  <Label>
                    {activeSubregion}
                    <Icon onClick={() => handleSubClose()} name="delete" />{' '}
                  </Label>
                ) : (
                  ''
                )}
              </Menu.Item>
            </Menu>
          ) : (
            <></>
          )}
        </>
      )}
    </>
  )
}

export default HeaderNav
